<template>
	<div id="alphabet-wrap">
		<my-header></my-header>
		<h1>The Morse Code Alphabet</h1>
		<ul>
			<li v-for="item in alphabet" class="alphabet-list">
				<div class="word">{{ item.word }}</div>
				<div class="button">
					<a href="#" v-on:click.prevent="play(item.word)"><font-awesome-icon icon="play-circle" /></a>
				</div>
				<span class="code">{{ item.code }}</span>
				<span></span>
			</li>
		</ul>
		<my-footer></my-footer>
	</div>
</template>
<script>
	import header from './myHeader.vue';
	import footer from './myFooter.vue';

	export default {
		data() {
			var M_context = new AudioContext();
			var M_analyser = M_context.createAnalyser();
			M_analyser.connect(M_context.destination);
			M_analyser.fftSize = 8192;

			return {

				M_context,
				M_analyser,
				code: '',
				alphabet: [
					{word:'a', code:'.-'},
					{word:'b', code:'-...'},
					{word:'c', code:'-.-.'},
					{word:'d', code:'-..'},
					{word:'e', code:'.'},
			        {word:'f', code:'..-.'},
			        {word:'g', code:'--.'},
			        {word:'h', code:'....'},
			        {word:'i', code:'..'}, 
			        {word:'j', code:'.---'},
			        {word:'k', code:'-.-'},
			        {word:'l', code:'.-..'},
			        {word:'m', code:'--'},
			        {word:'n', code:'-.'},
			        {word:'o', code:'---'},
			        {word:'p', code:'.--.'},
			        {word:'q', code:'--.-'},
			        {word:'r', code:'.-.'},
			        {word:'s', code:'...'},
			        {word:'t', code:'-'},
			        {word:'u', code:'..-'},
			        {word:'v', code:'...-'},
			        {word:'w', code:'.--'},
			        {word:'x', code:'-..-'},
			        {word:'y', code:'-.--'},
			        {word:'z', code:'--..'}
				]
			}
		},
		components: {
    		myHeader: header,
    		myFooter: footer
	  	},
	  	mounted() {
	  		const vm = this;

	  			vm.morse = new vm.MorseCodec();
	  			vm.M_morse = new vm.MorseCodec();
				vm.morsePlayer = new vm.MorseCodePlayer(vm.M_context, vm.M_analyser, vm.M_morse); 
				vm.init();

				vm.bufferLength = vm.M_analyser.frequencyBinCount;
				vm.dataArray = new Uint8Array(vm.bufferLength);
	  	},
	  	methods: {
	  		MorseCodec: function() {

	  			const vm = this;
	  			
	  			vm.ALPHABET = {
		        // letters:
		        'a': '.-',   'b': '-...', 'c': '-.-.', 'd': '-..', 'e': '.',
		        'f': '..-.', 'g': '--.',  'h': '....', 'i': '..',  'j': '.---',
		        'k': '-.-',  'l': '.-..', 'm': '--',   'n': '-.',  'o': '---',
		        'p': '.--.', 'q': '--.-', 'r': '.-.',  's': '...', 't': '-',
		        'u': '..-',  'v': '...-', 'w': '.--',  'x': '-..-','y': '-.--',
		        'z': '--..',
		        // digits:
		        '1': '.----', '2': '..---', '3': '...--', '4': '....-', '5': '.....',
		        '6': '-....', '7': '--...', '8': '---..', '9': '----.', '0': '-----',
		        // misc:
		        '.': '.-.-.-', ',': '--..--', '?': '..--..',  '\'': '.----.',
		        '/': '-..-.',  '(': '-.--.',  ')': '-.--.-',  '&': '.-...',
		        ':': '---...', ';': '-.-.-.', '=': '-...-',   '+': '.-.-.',
		        '-': '-....-', '-': '..--.-', '\"': '.-..-.', '$': '...-..-',
		        '!': '-.-.--', '@': '.--.-.'
	    		};
			    /** Number of time units between symbols */
			    vm.SHORT_GAP = 3;
			    /** Number of time units between words */
			    vm.MEDIUM_GAP = 7;

			    vm.encode = function(text) {
			    	var cleanText = text.trim().replace(/\s+/, '');
				    return [].map.call(cleanText.toLowerCase(),
				        function(s) {
				            return vm.ALPHABET[s];
				        }, vm)
				       .join('');
			    }

			    vm.encodeWithSpacing = function(text) {
			    	var wordGap = Array(this.MEDIUM_GAP + 1).join(' ');
				    var symbolGap = Array(this.SHORT_GAP + 1).join(' ');

				    var words = text.trim().replace(/\s+/, ' ').split(' ');

				    var code = [];
				    for (var i = 0; i < words.length; i++) {
				        code.push(
				            [].map.call(words[i].toLowerCase(),
				                function(s) {
				                    return this.ALPHABET[s].split('').join(' ');
				                }, this)
				            .join(symbolGap)
				        );
				    }
				    return code.join(wordGap);
			    }

			    vm.getSymbolByCode = function(code) {
				    for (var symbol in vm.ALPHABET) {
				        if (vm.ALPHABET[symbol] === code) {
				            return symbol;
				        }
				    }
				}				
			},
			MorseCodePlayer: function(cntxt, analyzer, mp) {
				const vm = this;

				// console.log(vm.M_context);
				vm.DOT_DURATION = 75;
			    vm.DASH_DURATION = vm.DOT_DURATION * 3;
			    /** Frequency by default is D5 */
			    vm.TONE = 587;
			    /** By default create sound buffer with single tone */
			    
			    vm.isPlaying = false;

			    vm.createToneSignal = function(len, freq) {
			    	len *= cntxt.sampleRate / 1000;
				    var buffer = cntxt.createBuffer(1, len, cntxt.sampleRate);
				    var data = buffer.getChannelData(0);
				    var digitalFreq = 0.1;
				    if (freq) {
				        digitalFreq = 2 * Math.PI * freq / cntxt.sampleRate;
				    }
				    for (var i = 0; i < len; i++) {
				        data[i] = Math.sin(digitalFreq * i);
				    }
				    return buffer;
			    }

			    vm.setDotSound = function(decodedArrayBuffer, obj) {
			    	var self = obj ? obj : this;
				    self.dotSource = cntxt.createBufferSource();
				    self.dotSource.buffer = decodedArrayBuffer;
				    self.dotSource.connect(analyzer);
			    }

			    vm.setDashSound = function(decodedArrayBuffer, obj) {
			    	var self = obj ? obj : this;
				    self.dashSource = cntxt.createBufferSource();
				    self.dashSource.buffer = decodedArrayBuffer;
				    self.dashSource.connect(analyzer);
			    }

			    vm.loadSoundSet = function(setNo) {
			    	if (setNo === 0) {
				        var dashBuffer = vm.createToneSignal(vm.DASH_DURATION, vm.TONE);
				        vm.setDashSound(dashBuffer);

				        var dotBuffer = vm.createToneSignal(vm.DOT_DURATION, vm.TONE);
				        vm.setDotSound(dotBuffer);
				    }
			    }
			    vm.loadSoundSet(0);

			    vm.playText = function(text) {
			    	var vm = this;

			    	vm.isPlaying = true;
    				vm.playSymbol(mp.encodeWithSpacing(text));
			    }

			    vm.playSymbol = function(text) {
			    	if (text.length === 0 || !vm.isPlaying) {
				        vm.stop();
				        return;
				    }
				    var pause = vm.DOT_DURATION;
				    if (text[0] === '.') {
				        vm.dotSource.start(0);
				        vm.setDotSound(vm.dotSource.buffer);
				        // update();
				    }
				    else if (text[0] === '-'){
				        vm.dashSource.start(0);
				        vm.setDashSound(vm.dashSource.buffer);
				        pause = vm.DASH_DURATION;
				        // update();
				    }
				    setTimeout(function(self) {
				                  self.playSymbol(text.slice(1));
				               },
				               pause, this);
			    }

			    vm.stop = function() {
			    	vm.isPlaying = false;
			    }
			},

			init: function() {
				const vm = this;
				vm.morsePlayer.loadSoundSet(0);
			},
			// updateLabels: function() {
			// 	const vm = this;
			// 	vm.code = vm.decodedLabel.textContent;
			//     vm.played = vm.playedLabel.textContent;

			//     vm.played += vm.code[0];
			//     vm.code = vm.code.slice(1);

			//     vm.decodedLabel.textContent = vm.code;
			//     vm.playedLabel.textContent = vm.played;

			//     if (vm.code === '') {
			//         vm.playButton.textContent = 'Say!';
			//     }
			// },
			play: function(word) {
				const vm = this;

				if (vm.morsePlayer.isPlaying) {
			        vm.stopM();
			    }
			    else {
			        var message = word;
			        if (message === '') {
			            return;
			        }
			        vm.code = vm.morse.encode(message);
			        // vm.decodedLabel.textContent = vm.code;
			        vm.morsePlayer.playText(message);
			    }
			},
			stopM: function() {
				const vm = this;
    			vm.morsePlayer.stop();
			},
	  	}
	}
</script>
<style>

	/*@import url('https://fonts.googleapis.com/css?family=Staatliches');*/

	#alphabet-wrap h1, h3 {
		font-family: 'Staatliches', cursive;
		letter-spacing: 4px;
		margin-top: 50px;
		margin-bottom: 50px;
		color: #c2185b;
		text-align: center;
	}

	#alphabet-wrap h1 {
		font-size: 50px;
		text-shadow: 1px 2px 3px #ffc1e3;
	}

	.alphabet-list {
		position: relative;
		background-color: rgba(194, 24, 91, 0.6);
		font-size: 35px;
		letter-spacing: 9px;
		list-style: none;
		margin-bottom: 20px;
		vertical-align: middle;
	}

	.word {
		display: inline-block;
		width: 70px;
		text-transform: uppercase;
		margin-left: 15px;
		font-family: 'ZCOOL KuaiLe', cursive;
		text-shadow: 2px 2px 5px #000;
		color: #ab47bc;
		font-size: 45px;

	}
	
	.button {
		display: inline-block;
		width: 80px;
		font-size: 50px;
	}

	.button a {
		color: #7986cb;
	}

	.button a:hover {
		color: #49599a;
	}		

	.code {
		position: absolute;
		right: 20px;
		font-size: 55px;
		letter-spacing: 25px;
		font-weight: bold;
		text-shadow: 2px 2px 5px #000;
		color: #ab47bc;
	}

</style>