<template>
	<div id="wrap">
		<my-header/>
	    <div id="main">
	    	<h1 class="header">Welcome to Morseuse!</h1>
	    	<h3>A Morse trainer to make the process of learning Morse code more useful</h3>
	    	<p class="try">
	    		Just try It:
	    	</p>
	        <form>
	            <input class="input" type="text" maxlength="25" ref="input"/>
	            <b-button id="say-button" variant="primary" type="button" ref="playButton" v-on:click="play()">Say!</b-button>
	            <p>
	                <em id="played-message" ref="playedMessage"></em><span id="decoded-message" ref="decodedMessage"></span>
	            </p>
	            <div class="canvas-wrap">
	            	<canvas ref="canvas" id="inactive-visualize"></canvas>
	            </div>
	        </form>
	    </div>
	    <my-footer class="my-footer"/>
	</div>
</template>
<script>
	import header from './myHeader.vue';
	import footer from './myFooter.vue';

	export default {
		data () {
			var M_context = new AudioContext();
			var M_analyser = M_context.createAnalyser();
			M_analyser.connect(M_context.destination);
			M_analyser.fftSize = 8192;

			return {
				M_context,
				M_analyser,
				code: ''
			}
		},
  		components: {
    		myHeader: header,
    		myFooter: footer
	  	},
	  	created() {

	  	},
	  	methods: {
	  		MorseCodec: function() {

	  			const vm = this;
	  			
	  			vm.ALPHABET = {
		        // letters:
		        'a': '.-',   'b': '-...', 'c': '-.-.', 'd': '-..', 'e': '.',
		        'f': '..-.', 'g': '--.',  'h': '....', 'i': '..',  'j': '.---',
		        'k': '-.-',  'l': '.-..', 'm': '--',   'n': '-.',  'o': '---',
		        'p': '.--.', 'q': '--.-', 'r': '.-.',  's': '...', 't': '-',
		        'u': '..-',  'v': '...-', 'w': '.--',  'x': '-..-','y': '-.--',
		        'z': '--..',
		        // digits:
		        '1': '.----', '2': '..---', '3': '...--', '4': '....-', '5': '.....',
		        '6': '-....', '7': '--...', '8': '---..', '9': '----.', '0': '-----',
		        // misc:
		        '.': '.-.-.-', ',': '--..--', '?': '..--..',  '\'': '.----.',
		        '/': '-..-.',  '(': '-.--.',  ')': '-.--.-',  '&': '.-...',
		        ':': '---...', ';': '-.-.-.', '=': '-...-',   '+': '.-.-.',
		        '-': '-....-', '-': '..--.-', '\"': '.-..-.', '$': '...-..-',
		        '!': '-.-.--', '@': '.--.-.'
	    		};
			    /** Number of time units between symbols */
			    vm.SHORT_GAP = 3;
			    /** Number of time units between words */
			    vm.MEDIUM_GAP = 7;

			    vm.encode = function(text) {
			    	var cleanText = text.trim().replace(/\s+/, '');
				    return [].map.call(cleanText.toLowerCase(),
				        function(s) {
				            return vm.ALPHABET[s];
				        }, vm)
				       .join('');
			    }

			    vm.encodeWithSpacing = function(text) {
			    	var wordGap = Array(this.MEDIUM_GAP + 1).join(' ');
				    var symbolGap = Array(this.SHORT_GAP + 1).join(' ');

				    var words = text.trim().replace(/\s+/, ' ').split(' ');

				    var code = [];
				    for (var i = 0; i < words.length; i++) {
				        code.push(
				            [].map.call(words[i].toLowerCase(),
				                function(s) {
				                    return this.ALPHABET[s].split('').join(' ');
				                }, this)
				            .join(symbolGap)
				        );
				    }
				    return code.join(wordGap);
			    }

			    vm.getSymbolByCode = function(code) {
				    for (var symbol in vm.ALPHABET) {
				        if (vm.ALPHABET[symbol] === code) {
				            return symbol;
				        }
				    }
				}				
			},
			MorseCodePlayer: function(cntxt, analyzer, mp) {
				const vm = this;

				vm.DOT_DURATION = 75;
			    vm.DASH_DURATION = vm.DOT_DURATION * 3;

			    vm.TONE = 587;
			    
			    vm.isPlaying = false;

			    vm.createToneSignal = function(len, freq) {
			    	len *= cntxt.sampleRate / 1000;
				    var buffer = cntxt.createBuffer(1, len, cntxt.sampleRate);
				    var data = buffer.getChannelData(0);
				    var digitalFreq = 0.1;
				    if (freq) {
				        digitalFreq = 2 * Math.PI * freq / cntxt.sampleRate;
				    }
				    for (var i = 0; i < len; i++) {
				        data[i] = Math.sin(digitalFreq * i);
				    }
				    return buffer;
			    }

			    vm.setDotSound = function(decodedArrayBuffer, obj) {
			    	var self = obj ? obj : this;
				    self.dotSource = cntxt.createBufferSource();
				    self.dotSource.buffer = decodedArrayBuffer;
				    self.dotSource.connect(analyzer);
			    }

			    vm.setDashSound = function(decodedArrayBuffer, obj) {
			    	var self = obj ? obj : this;
				    self.dashSource = cntxt.createBufferSource();
				    self.dashSource.buffer = decodedArrayBuffer;
				    self.dashSource.connect(analyzer);
			    }

			    vm.loadSoundSet = function(setNo) {
			    	if (setNo === 0) {
				        var dashBuffer = vm.createToneSignal(vm.DASH_DURATION, vm.TONE);
				        vm.setDashSound(dashBuffer);

				        var dotBuffer = vm.createToneSignal(vm.DOT_DURATION, vm.TONE);
				        vm.setDotSound(dotBuffer);
				    }
			    }
			    vm.loadSoundSet(0);

			    vm.playText = function(text, update) {
			    	var vm = this;

			    	vm.isPlaying = true;
    				vm.playSymbol(mp.encodeWithSpacing(text), update);
			    }

			    vm.playSymbol = function(text, update) {
			    	if (text.length === 0 || !vm.isPlaying) {
				        vm.stop();
				        return;
				    }
				    var pause = vm.DOT_DURATION;
				    if (text[0] === '.') {
				        vm.dotSource.start(0);
				        vm.setDotSound(vm.dotSource.buffer);
				        update();
				    }
				    else if (text[0] === '-'){
				        vm.dashSource.start(0);
				        vm.setDashSound(vm.dashSource.buffer);
				        pause = vm.DASH_DURATION;
				        update();
				    }
				    setTimeout(function(self) {
				                  self.playSymbol(text.slice(1), update);
				               },
				               pause, this);
			    }

			    vm.stop = function() {
			    	vm.isPlaying = false;
			    }
			},

			init: function() {
				const vm = this;
				console.log(vm.morsePlayer);
				vm.morsePlayer.loadSoundSet(0);
			},
			updateLabels: function() {
				const vm = this;
				vm.code = vm.decodedLabel.textContent;
			    vm.played = vm.playedLabel.textContent;

			    vm.played += vm.code[0];
			    vm.code = vm.code.slice(1);

			    vm.decodedLabel.textContent = vm.code;
			    vm.playedLabel.textContent = vm.played;

			    if (vm.code === '') {
			        vm.playButton.textContent = 'Say!';
			    }
			},
			play: function() {
				const vm = this;

				if (vm.morsePlayer.isPlaying) {
			        vm.stopM();
			    }
			    else {
			        var message = vm.textInput.value;
			        if (message === '') {
			            return;
			        }
			        vm.code = vm.morse.encode(message);
			        vm.decodedLabel.textContent = vm.code;
			        vm.playedLabel.textContent = '';
			        vm.playButton.textContent = 'Stop';
			        vm.morsePlayer.playText(message, vm.updateLabels);
			        vm.activateCanvas();
			        vm.draw();
			    }
			},
			stopM: function() {
				const vm = this;
				vm.playButton.textContent = 'Say!';
    			vm.morsePlayer.stop();
			},

			activateCanvas: function() {
				this.canvas.setAttribute('id', 'active-visualize');
			},
			deactivateCanvas: function() {
				this.canvas.setAttribute('id', 'inactive-visualize');
			},
			draw: function() {
				const vm = this;
				if (!this.morsePlayer.isPlaying) {
			        setTimeout(this.deactivateCanvas, 300);
			        return;
			    }

			    requestAnimationFrame(vm.draw);

			    this.M_analyser.getByteTimeDomainData(this.dataArray);

			    vm.canvasCtx.clearRect(0, 0, vm.canvas.width, vm.canvas.height);
			    vm.canvasCtx.lineWidth = 1;
			    vm.canvasCtx.strokeStyle = 'rgb(255, 255, 0)';
			    vm.canvasCtx.beginPath();

			    var sliceWidth = vm.canvas.width * 1.0 / this.bufferLength;
			    var x = 0;

			    for (var i = 0; i <  this.bufferLength; i++) {
			        var v = this.dataArray[i] / 128.0;
			        var y = v * vm.canvas.height / 2;
			        if (i === 0) {
			            vm.canvasCtx.moveTo(x, y);
			        } else {
			            vm.canvasCtx.lineTo(x, y);
			        }
			        x += sliceWidth;
			    }

			    vm.canvasCtx.lineTo(vm.canvas.width, vm.canvas.height / 2);
			    vm.canvasCtx.stroke();
			}


	  	},
	  	mounted() {

	  			const vm = this;

	  			vm.morse = new vm.MorseCodec();
	  			vm.M_morse = new vm.MorseCodec();
				vm.morsePlayer = new vm.MorseCodePlayer(vm.M_context, vm.M_analyser, vm.M_morse); 
				vm.init();

				vm.textInput = vm.$refs.input;
				vm.decodedLabel = vm.$refs.decodedMessage;
				vm.playedLabel = vm.$refs.playedMessage;
				vm.playButton = vm.$refs.playButton;

				vm.bufferLength = vm.M_analyser.frequencyBinCount;
				vm.dataArray = new Uint8Array(vm.bufferLength);

				vm.canvas = vm.$refs.canvas;
				vm.canvasCtx = vm.canvas.getContext("2d");
				vm.played = '';
		}
	}

</script>
<style scoped>

	#wrap {
		height: 100vh;
		overflow-y: hidden;
	}

	h1, h3 {
		font-family: 'Staatliches', cursive;
		letter-spacing: 4px;
		margin-top: 50px;
		color: #bbdefb;
	}

	h1 {
		font-size: 50px;
		text-shadow: 1px 2px 3px #8aacc8;
	}

	#main {
		text-align: center;
	    align-content: center;
	    align-items: center;
	    font-family: 'Indie Flower', cursive;
	    font-size: 1.3em;
	    color: white;
	    margin: 0 auto;
	    margin-top: 30px;
	    margin-bottom: 60px;
	    word-wrap: break-word;
	}

	form {
		text-align: center;
	}

	.try {
		font-size: 30px;
		margin-top: 20px;
	}

	.input {
	    width: 350px;
	    height: 40px;
	    margin: 0 20px 50px 20px;
	    text-align: center;
	    border: 1px rgba(255,255,255,0.2) solid;
	    border-radius: 5px;
	    padding: 5px;
	    font-family: 'Indie Flower', cursive;
	    font-size: 20px;
	    color: white;
	    background-color: rgba(254,254,255,0.1);
	}

	.input:focus {
	    outline: 0;
	    border-color: rgba(250,220,190,0.8);
	    -webkit-box-shadow: inset 0 1px 1px rgba(250,220,190,.1), 0 0 8px rgba(255,255,255,0.6);
	    box-shadow: 0 0 1px 1px rgba(250,220,190,.4);
	}

	#say-button {
	    width: 80px;
	}

	#say-button:hover, #say-button:active {
	    background-color: rgb(250,250,250);
	    color: black;
	    outline: 0;
	}

	#decoded-message, #played-message {
		font-size: 40px;
	    margin: 0;
	    padding: 0;
	}

	#played-message {
	    text-align: center;
	    color: yellow;
	    height: 50px;
	}

	#main canvas {
	    width: 360px;
	    padding: 10px;
	}
		
	.canvas-wrap {
		height: 50px;
	}

	#active-visualize {
	    height: 50px;
	    border: 1px solid rgba(255,255,255,0.5);
	    border-radius: 10px;
	}
	
	#inactive-visualize {
	    height: 0px;
	}

	.my-footer {
		position: absolute;
	}
</style>